import React, { Component, Fragment } from 'react';
import { withRouter } from 'react-router-dom';
import moment from 'moment';
import Page from 'components/LayoutComponents/Page';
import { Row, Col, Card, Form, Input, Select, DatePicker, InputNumber, Menu, Icon } from 'antd';
import { BtnSave, BtnCancel } from 'components/common/button';
import { schoolYearsTime } from 'dump/data';
import TableForFormEntering from './TableForFormEntering';

@Form.create({ name: 'FormEnteringInfoQD' })
class FormEnteringInfoQD extends Component {
  state = {};

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  handleClick = e => {
    this.setState({
      current: e.key,
    });
    if (e.key === 'enteringInfo') {
      console.log('render');
    }
  };

  render() {
    const { Option } = Select;
    const { TextArea } = Input;
    const dateFormatList = ['DD/MM/YYYY', 'DD/MM/YY'];
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    // const { scholarshipRatingsId } = this.state;
    return (
      <Card title="Nhập Quyết định thôi học, ngừng học">
        <Row className="rowMargin">
          <Col span={24}>
            <Menu onClick={this.handleClick} selectedKeys={[this.state.current]} mode="horizontal">
              <Menu.Item key="update">
                <Icon type="save" theme="twoTone" twoToneColor="#6699ff" />
                Cập nhật
              </Menu.Item>
              <Menu.Item key="add">
                <Icon type="plus-circle" theme="twoTone" twoToneColor="#33cc33" />
                Bổ sung
              </Menu.Item>
              <Menu.Item key="delete">
                <Icon type="delete" theme="twoTone" twoToneColor="#ff3333" />
                Xóa
              </Menu.Item>
            </Menu>
          </Col>
        </Row>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit}>
            <Form.Item label="Học kì" {...formItemLayout}>
              {getFieldDecorator('semester', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<InputNumber min={1} max={3} defaultValue={1} style={{ width: '100%' }} />)}
            </Form.Item>
            <Form.Item label="Năm học" {...formItemLayout}>
              {getFieldDecorator('schoolYearsTime', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select>
                  {schoolYearsTime.map(item => (
                    <Option key={item.code} value={item.code}>
                      {item.name}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Số QĐ">
              {getFieldDecorator('decisionNumber', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Ngày QĐ">
              {getFieldDecorator('decisionDate', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Fragment>
                  <DatePicker defaultValue={moment('30/04/2019', dateFormatList[0])} />
                </Fragment>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Loại QĐ">
              {getFieldDecorator('typeDecision', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select>
                  <Option value="1">Ngừng học</Option>
                  <Option value="2">Thôi học</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Nội dung">
              {getFieldDecorator('contentDecision', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<TextArea style={{ height: 100 }} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
        <Row>
          <TableForFormEntering />
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormEnteringInfoQD {...props} />
  </Page>
));



